.dashboardContainer {
    height: 100vh;
    overflow: hidden;
    width: 100vw;

    .svgIconsMenu {
        transform: scale(0.889) !important;
        color: #FFF !important;
    }

    .hamburgerwrapper {
        height: 55px;
        margin-bottom: 16px;
        border-bottom: 1px solid rgba(112, 112, 112, 0.15);

        .MuiButtonBase-root {
            margin: 8px 8px 8px;
            border-radius: 0;
            display: flex;
            align-items: center;
        }
    }

    .menuStyle {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
    }

    .linkStyle {
        height: 40px;
        margin: 8px;
        border-radius: 4px;
    }

    .linkStyle:hover {
        background: rgb(0, 0, 0, 0.15);

        img {
            filter: invert(76%) sepia(44%) saturate(455%) hue-rotate(111deg) brightness(90%) contrast(86%);
        }
    }

    .inActiveMenu {
        background: none;

        img {
            filter: invert(100%) sepia(91%) saturate(38%) hue-rotate(53deg) brightness(110%) contrast(110%);
        }

        .menuName {
            color: #FFF;
        }
    }

    .activeMenu {
        background: #106AA5;

        img {
            filter: invert(100%) sepia(91%) saturate(38%) hue-rotate(53deg) brightness(110%) contrast(110%);
        }

        .menuName {
            color: #FFF;
        }
    }

    .activeMenu:hover {
        background: #106AA5;

        img {
            filter: invert(100%) sepia(91%) saturate(38%) hue-rotate(53deg) brightness(110%) contrast(110%);
        }

        .menuName {
            color: #FFF;
        }
    }

    .expandsideNav {
        -moz-animation: slideOpen 0.5s ease 0s forwards;
        -webkit-animation: slideOpen 0.5s ease 0s forwards;
        -o-animation: slideOpen 0.5s ease 0s forwards;
        -ms-animation: slideOpen 0.5s ease 0s forwards;
        animation: slideOpen 0.5s ease 0s forwards;
        z-index: 100;

        .settingStyle {
            animation: settingSlideopen 0.9s ease 0s forwards;
        }

        @keyframes settingSlideopen {
            from {
                width: 40px;
            }

            to {
                width: 240px;
            }
        }

        @-moz-keyframes settingSlideopen

        /* Firefox */
            {
            from {
                width: 40px;
            }

            to {
                width: 240px;
            }
        }

        @-webkit-keyframes settingSlideopen

        /* Safari and Chrome */
            {
            from {
                width: 40px;
            }

            to {
                width: 240px;
            }
        }

        @-o-keyframes settingSlideopen

        /* Opera */
            {
            from {
                width: 40px;
            }

            to {
                width: 240px;
            }
        }

        @-ms-keyframes settingSlideopen

        /* IE10 */
            {
            from {
                width: 40px;
            }

            to {
                width: 240px;
            }
        }
    }

    @-moz-keyframes slideOpen

    /* Firefox */
        {
        from {
            width: 56px;
        }

        to {
            width: 256px;
        }
    }

    @-webkit-keyframes slideOpen

    /* Safari and Chrome */
        {
        from {
            width: 56px;
        }

        to {
            width: 256px;
        }
    }

    @-o-keyframes slideOpen

    /* Opera */
        {
        from {
            width: 56px;
        }

        to {
            width: 256px;
        }
    }

    @-ms-keyframes slideOpen

    /* IE10 */
        {
        from {
            width: 56px;
        }

        to {
            width: 256px;
        }
    }

    @keyframes slideOpen {
        from {
            width: 56px;
        }

        to {
            width: 256px;
        }
    }

    .expandedAnimation {
        width: calc(100% - 40px);
        -moz-animation: textanimate 2s ease 0s forwards;
        -webkit-animation: textanimate 2s ease 0s forwards;
        -o-animation: textanimate 2s ease 0s forwards;
        -ms-animation: textanimate 2s ease 0s forwards;
        animation: textanimate 2s ease 0s forwards;
    }

    @keyframes textanimate {
        from {
            opacity: 0;
            display: none;
        }

        to {
            opacity: 1;
            display: block;
        }
    }

    @-moz-keyframes textanimate

    /* Firefox */
        {
        from {
            opacity: 0;
            display: none;
        }

        to {
            opacity: 1;
            display: block;
        }
    }

    @-webkit-keyframes textanimate

    /* Safari and Chrome */
        {
        from {
            opacity: 0;
            display: none;
        }

        to {
            opacity: 1;
            display: block;
        }
    }

    @-o-keyframes textanimate

    /* Opera */
        {
        from {
            opacity: 0;
            display: none;
        }

        to {
            opacity: 1;
            display: block;
        }
    }

    @-ms-keyframes textanimate

    /* IE10 */
        {
        from {
            opacity: 0;
            display: none;
        }

        to {
            opacity: 1;
            display: block;
        }
    }

    .shrinksideNav {
        -moz-animation: slideClose 0.5s ease 0s forwards;
        -webkit-animation: slideClose 0.5s ease 0s forwards;
        -o-animation: slideClose 0.5s ease 0s forwards;
        -ms-animation: slideClose 0.5s ease 0s forwards;
        animation: slideClose 0.5s ease 0s forwards;


        .settingStyle {
            animation: settingSlideclose 0.4s ease 0s forwards;
        }

        @keyframes settingSlideclose {
            from {
                width: 240px;
            }

            to {
                width: 40px;
            }
        }

        @-moz-keyframes settingSlideclose

        /* Firefox */
            {
            from {
                width: 240px;
            }

            to {
                width: 40px;
            }
        }

        @-webkit-keyframes settingSlideclose

        /* Safari and Chrome */
            {
            from {
                width: 240px;
            }

            to {
                width: 40px;
            }
        }

        @-o-keyframes settingSlideclose

        /* Opera */
            {
            from {
                width: 240px;
            }

            to {
                width: 40px;
            }
        }

        @-ms-keyframes settingSlideclose

        /* IE10 */
            {
            from {
                width: 240px;
            }

            to {
                width: 40px;
            }
        }
    }

    @keyframes slideClose {
        from {
            width: 256px;
        }

        to {
            width: 56px;
        }
    }

    @-moz-keyframes slideClose

    /* Firefox */
        {
        from {
            width: 256px;
        }

        to {
            width: 56px;
        }
    }

    @-webkit-keyframes slideClose

    /* Safari and Chrome */
        {
        from {
            width: 256px;
        }

        to {
            width: 56px;
        }
    }

    @-o-keyframes slideClose

    /* Opera */
        {
        from {
            width: 256px;
        }

        to {
            width: 56px;
        }
    }

    @-ms-keyframes slideClose

    /* IE10 */
        {
        from {
            width: 256px;
        }

        to {
            width: 56px;
        }
    }


    .settingStyle {
        position: fixed;
        bottom: 0;
    }
}

@media only screen and (max-width: 1000px) {
    .dashboardContainer {
        overflow: initial;

        .shrinksideNav {
            z-index: 999;
        }


        .hamburgerwrapper {
            margin-bottom: initial;
        }

        .shrinksideNav {
            -moz-animation: slideClose 0.5s ease 0s forwards;
            -webkit-animation: slideClose 0.5s ease 0s forwards;
            -o-animation: slideClose 0.5s ease 0s forwards;
            -ms-animation: slideClose 0.5s ease 0s forwards;
            animation: slideClose 0.5s ease 0s forwards;
        }

        @keyframes slideClose {
            from {
                width: 256px;
            }

            to {
                width: 0;
            }
        }

        @-moz-keyframes slideClose

        /* Firefox */
            {
            from {
                width: 256px;
            }

            to {
                width: 0;
            }
        }

        @-webkit-keyframes slideClose

        /* Safari and Chrome */
            {
            from {
                width: 256px;
            }

            to {
                width: 0;
            }
        }

        @-o-keyframes slideClose

        /* Opera */
            {
            from {
                width: 256px;
            }

            to {
                width: 0;
            }
        }

        @-ms-keyframes slideClose

        /* IE10 */
            {
            from {
                width: 256px;
            }

            to {
                width: 0;
            }
        }
    }
}