.headerContainer {
    .profileSection {
        background-color: #FFFFFF;
        height: 40px;
        border-radius: 20px;
        margin-right: 8px;
    }

    .brandContainer {
        height: 32px;
        width: 32px;
        display: flex;
        margin-left: 4px;
        background: white;
        border-radius: 20px;

        img {
            border-radius: 20px;
            width: 100%;
            object-fit: cover;
            height: 100%;
        }
    }

    .profiledetail {
        img {
            width: 32px;
            background: #CBD7BE;
            height: 32px;
            border-radius: 20px;
            // margin-right: 11px;
        }
    }
}


@media only screen and (max-width: 1000px) {
    .headerContainer {
        position: fixed;
        top: 0;
        z-index: 99;
        width: calc(100% - 56px);
    }
}